* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}

h1 {
  font-family: "Poiret One", cursive;
  font-size: 2.188rem;
  font-weight: 900;
}

p {
  font-family: 'Source Serif Pro', serif;
  font-size: 1.25rem;
}

/* Styles for Navigation page */

.nav-container {
  background-color: #fafafa;
  width: 100%;
  max-width: 100%;
  position: fixed;
  left: 0%;
  top: 0%;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
  z-index: 1;
  padding: .3rem;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo h2 {
  font-family: "Arvo", serif;
  text-transform: uppercase;
  font-size: 2rem;
  text-align: center;
  color: #4169e1;
}

h4 {
  text-align: center;
  font-family: "Poiret One", cursive;
  letter-spacing: 2px;
  margin: 5.2% 0 .6rem 0;
}

.menu {
  position: absolute;
  top: 15%;
  right: 1%;
  font-size: 2.2rem;
  z-index: 1;
  margin: .2rem;
  cursor: pointer;
}

.menu:hover {
  color: #4169e1;
  transition: .7s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.close {
  position: absolute;
  right: 2%;
  top: .5%;
  color: #fff;
  cursor: pointer;
  font-size: 2.2rem;
}

.close:hover {
  color: #4169e1;
  transition: .7s cubic-bezier(0.165, 0.84, 0.44, 1);
}

nav {
  display: flex;
  flex-direction: column;
  background-color: #2c2c2c;
  position: fixed;
  z-index: 1;
  top: 0px;
  margin-top: 0px;
  left: 0;
  bottom: 0;
  width: 200px;
  min-width: 200px;
  height: 100%;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  transition: transform 1s;
  transform: translateX(-100%);
}

nav a {
  display: flex;
  justify-content: center;
  text-decoration: none;
  margin: 1.5rem 0;
  padding: .4rem 1rem;
  font-family: "Source Serif Pro", serif;
  font-size: large;
  color: #fff;
  border-bottom: 2px solid #fff;
}

nav a:hover {
  color: #4169e1;
  transition: .7s cubic-bezier(0.165, 0.84, 0.44, 1);
}

nav.show {
  transform: translate(0%);
}

@media (max-width: 480px) {
  .menu {
    font-size: x-large;
  }
  nav {
    width: 100%;
    max-width: 100%;
  }
}

@media (max-width: 1200px) {
  h4 {
   margin: 6% 0 .4rem 0;
  }
}

@media (max-width: 900px) {
   h4 {
    margin: 8% 0 .4rem 0;
   }
}

@media (max-width: 800px) {
  .logo h2 {
     font-size: x-large;
  }
  h4 {
    font-size: small;
    padding: .2rem .4rem;
    margin: 8% 0 .4rem 0;
  }
  .menu {
    position: absolute;
    top: -5%;
    right: 0%;
  }
}

@media (max-width: 480px) {
  .logo h2 {
    font-size: medium;
  }
  h4 {
    margin: 10% 0 .4rem 0;
  }
}

@media (max-width: 280px) {
  .home-container {
    max-width: 100%;
    overflow-x: hidden;
  }
  .logo h2 {
    font-size: large;
  }
  .home-container .content {
    width: 100%;
    position: relative;
    left: 25%;
  }
  h4 {
    font-size: small;
    padding: .2rem .4rem;
    margin: 14% 0 .4rem 0;
  }
  .p {
    font-family: "Source Serif Pro", serif;
    text-align: center;
    font-size: medium;
  }
}

/* End of styles for Navigation page */

/* Styles for Footer Page */

.footer {
  background-color: #343434;
  width: 100%;
  position: relative;
  bottom: 0%;
  left: 0%;
  padding: .5rem .4rem;
  color: #fff;
}

.footer .about {
  font-family: "Source Serif Pro", serif;
}

.footer .footer-wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.footer h2 {
  text-decoration: underline #4169e1;
  text-underline-offset: 10px;
  margin: .5rem 0;
  font-family: "Source Serif Pro", serif;
  font-size: large;
  text-align: center;
  text-transform: uppercase;
}

.footer .about p {
  text-align: justify;
  font-family: "Source Serif Pro", serif;
  font-size: calc(.75rem + .6vw);
  font-size: medium;
  line-height: 30px;
}

.footer .links {
  text-align: center;
}

.footer .links h2 {
  font-size: large;
}

.footer .links a {
  display: block;
  color: #fff;
  text-decoration: none;
  font-family: "Source Serif Pro", serif;
  text-align: center;
  font-size: medium;
  padding: .2rem 0;
}

.footer .links a:hover {
  color: green;
  transition: .7s cubic-bezier(0.165, 0.84, 0.44, 1);
}

.footer .details {
  text-align: justify;
}

.footer .details>div {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: .4rem;
}

.footer .details h2 {
  font-size: large;
}

.footer .details p {
  padding: 0 .4rem;
  font-family: "Source Serif Pro", serif;
  font-size: medium;
}

.footer a {
  padding: 0 .8rem;
  color: #4169e1;
  margin: 0 .5rem;
  font-family: "Source Serif Pro", serif;
  text-decoration: none;
}

.footer a:hover {
  color: lightgreen;
  transition: .7s cubic-bezier(0.165, 0.84, 0.44, 1);
}

hr {
  height: 1px;
  padding: 0%;
  margin: 0%;
}

.footer .media {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer .media p {
  font-family: "Source Serif Pro", serif;
  font-size: medium;
}

.footer .media .media-icons {
  height: 100%;
  min-width: 50px;
  padding: .4rem .5rem;
}

.footer .copy {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: medium;
}

.footer .copy p {
  font-size: medium;
  margin: .2rem 0;
}

.footer .copy a {
  display: flex;
  position: absolute;
  bottom: 3%;
  margin-bottom: 0%;
  align-items: flex-start;
  justify-content: flex-start;
  color: #fff;
}

.footer .copy a:hover {
  color: green;
}

/* responsive footer */
@media (max-width: 700px) {
  .footer .footer-wrapper {
    grid-template-columns: 1fr;
    align-items: center;
    justify-content: center;
    margin: 0 .8rem;
  }
  .footer .links a {
    margin: .6rem 0;
  }
  .footer .details {
    text-align: center;
    align-items: center;
    justify-content: center;
  }
  .footer .copy a {
    left: 0%;
    bottom: 1.5%;
  }
  .footer .media .media-icons {
    height: 40px;
    margin: 0;
  }
}

@media (max-width: 290px) {
    .footer .media {
      flex-wrap: wrap;
    }
}

/* end of styles for Footer Page */

/* styles for home page */

.home-container {
  max-width: 100%;
}

.home-container .headings {
  font-family: "Source Serif Pro", serif;
  text-align: center;
  font-size: larger;
  text-decoration: underline;
  text-underline-offset: 5px;
  margin: .8rem 0;
  color: #4169e1;
}

.home-container header img {
  width: 100%;
  max-width: 100%;
  height: 100%;
  min-height: 500px;
  border-radius: 2%;
  padding: 0 .8rem;
}

.home-container .hero {
  background-color: #002147;
  padding: .6rem 0;
}
.home-container .content {
  display: flex;
  flex-direction: column;
  margin: .4rem 2rem;
  padding: .4rem .8rem;
  align-items: center;
  justify-content: center;
  width: 90%;
}

.hero h2 {
  color: #fff;
  font-family: "Source Serif Pro", serif;
  font-size: x-large;
  text-align: center;
  border-radius: .5rem;
}

.home-container header p {
  font-family: "Source Serif Pro", serif;
  border-radius: .5rem;
  font-size: larger;
  text-align: center;
  color: #fff;
}

.home-main {
  background-color: #0f4d92;
  display: grid;
  max-width: 100%;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  text-align: center;
  padding: .2rem .5rem;
  justify-content: space-between;
}

.banner {
  background-color: #3399ff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: .4rem;
  border-radius: 1rem;
}

.banner .banner-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
}

.banner-wrapper img {
  width: 100%;
  min-height: 400px;
  border-radius: 1rem;
}

.banner-wrapper .Content {
  color: #fff;
  padding: .5rem;
  font-family: 'Source Serif Pro', serif;
  line-height: 32px;
}

.banner-wrapper .Content h1 {
  color: #002147;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: xx-large;
  margin: .4rem .2rem;
}

.banner-wrapper .Content p {
  text-align: justify;
  padding: .2rem .8rem;
  letter-spacing: 1.4px;
}

.banner-wrapper .Content .discount {
  background-color: rgba(0, 0, 0, .4);
  display: flex;
  width: 18vw;
  align-items: center;
  justify-content: center;
  padding: .4rem;
  border-radius: 1rem;
  color: rgba(255, 255, 255, .8);
  margin: .2rem .4rem;
}

.banner-wrapper .Content button {
  background-color: transparent;
  border: 2px solid #4169e1;
  border-radius: .8rem;
  padding: 10px 12px;
  text-transform: uppercase;
  font-size: medium;
  margin: .5rem 1rem;
  transition: .75s cubic-bezier(0.455, 0.03, 0.515, 0.955);
  cursor: pointer;
}

.banner-wrapper .Content button a {
  text-decoration: none;
  color: #fff;
}

.banner-wrapper .Content button:hover {
  background-color: #4169e1;
}

.home-main .cards {
  background: #002147;
  width: 90%;
  height: 100% auto;
  min-height: 220px;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: .8rem;
  padding: .4rem .6rem;
  margin: .6rem .8rem;
}

.home-main .cards .icons {
  font-size: calc(2.5rem + 1vw);
  color: #fff;
}

.home-main .card img {
  width: 50%;
}

.home-main .cards h2 {
  font-family: "Source Serif Pro", serif;
  color: lightblue;
  letter-spacing: 1.5px;
  font-size: large;
  text-transform: uppercase;
  margin: .5rem 0 0 0;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  left: 100%;
}

.btn button {
  width: 40%;
  background-color: #0f4d92;
  padding: .6rem;
  margin: .4rem 0;
  border: 2px solid green;
  text-transform: capitalize;
  border-radius: .5rem;
  transition: .75s cubic-bezier(0.455, 0.03, 0.515, 0.955);
}

.home-main button:hover {
  background-color: green;
}

.home-main button a {
  color: #fff;
  text-decoration: none;
}

.test-cont {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  justify-content: center;
}

.test-cont .test {
  padding: .6rem .8rem;
  margin: .8rem .5rem;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.test-cont .test img {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 150px;
  padding: .2rem .4rem;
  margin: .5rem auto;
  border-radius: 50%;
}

.test-cont .test h3 {
  font-family: "Source Serif Pro", serif;
  text-align: center;
  font-size: large;
  font-style: italic;
  color: #4169e1;
  margin: .5rem 0;
}

.test-cont .test p {
  color: #000;
  font-family: "Source Serif Pro", serif;
  font-size: calc(.8rem + .5vw);
  text-align: justify;
}

/* responsive home */
@media (max-width: 650px) {
  .home-container header img {
    width: 100%;
    height: 100%;
    min-height: 300px;
  }

  .home-container .hero {
    background-color: #002147;
    padding: .6rem 0;
  }
  .home-container .content {
    position: relative;
    display: flex;
    margin: .4rem 2rem;
    padding: .4rem .8rem;
    align-items: center;
    justify-content: center;
    width: 100%;
    left: -8%;
  }

  .home-container .content h2 {
    font-size: larger;
    width: 100%;
    padding: .2rem .4rem;
  }

  .home-container .content p {
    font-size: medium;
    width: 60vw;
    padding: .2rem .4rem;
    margin: .4rem 2rem;
    text-align: justify;
  }

  .home-main {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .home-main .cards {
    width: 90%;
    align-items: center;
    justify-content: center;
    height: auto;
  }

  .home-main h2 {
    font-size: large;
    text-align: center;
  }

  .btn button {
    width: auto;
    position: relative;
    left: -150%;
  }

  .banner .banner-wrapper {
    grid-template-columns: 1fr;
  }

  .banner-wrapper img {
    min-height: auto;
  }

  .banner-wrapper .Content {
    font-size: medium;
    text-align: center;
    align-items: center;
    justify-content: center;
  }

  .banner-wrapper .Content h1 {
    font-size: x-large;
  }

  .banner-wrapper .Content p {
    font-size: medium;
  }

  .banner-wrapper .Content .discount {
    font-size: medium;
    margin: auto;
    padding: .2rem;
    width: 35%;
  }

  .test-cont {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: justify;
    padding: .4rem 2rem;
  }

  .test-cont .test img {
    width: 100%;
    object-fit: contain;
  }

  .test-cont .test p {
    padding: .2rem .8rem;
    text-align: center;
  }
}

@media (max-width: 850px) {
  .home-main {
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: space-around;
  }

  .home-main .cards {
    align-items: center;
    justify-content: space-evenly;
    margin: .4rem;
  }

  .home-main .cards h2 {
    font-size: larger;
    margin: .4rem 0;
  }

  .btn {
    position: relative;
    left: 32%;
  }

  .test-cont {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 550px) {
  .home-container {
    overflow: hidden;
    max-width: 100%;
  }
  .home-container .content {
    margin: .4rem 2rem;
    padding: .4rem .8rem;
  }
  .home-container .content h2 {
    width: 100%;
    background: none;
  }
  .home-container .content p {
    width: 100%;
    padding: .2rem .5rem;
  }

}

@media (max-width: 280px) {
  .home-container {
    max-width: 100%;
    overflow: hidden;
  }
  .home-container .content {
    padding: .2rem .4rem;
    left: -12%;
  }
  .home-container .content h2 {
    width: 100%;
    font-size: larger;
  }
  .home-container .content p {
    width: 100%;
  }
  .btn {
    position: relative;
    left: 60%;
  }
  .banner-wrapper .Content .discount {
    width: 60%;
  }
  .test-cont .test h2 {
    font-size: small;
  }
  .test-cont .test p {
    padding: .1rem;
    margin: .1rem;
  }
}

/* End of styles for Home Page */

/* About Page styles */

.about-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
}

.about-cont h2 {
  text-align: center;
  color: #4169e1;
  text-decoration: underline;
  text-underline-offset: 5px;
  margin: .8rem 0;
}

.about-wrapper img {
  width: 100%;
}

.about-wrapper p {
  text-align: justify;
  padding: .2rem .8rem;
  line-height: 32px;
  font-size: calc(.5rem + .7rem);
}

.team {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
}

.team img {
  width: 100%;
  height: 60vh;
  shape-outside: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  -webkit-clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  object-position: center;
  object-fit: contain;
}

.team p {
  text-align: justify;
  font-family: "Source Serif Pro", serif;
  padding: .2rem .8rem;
  line-height: 32px;
}

.goal {
  margin: .2rem 5rem;
}

.goal h2 {
  margin: .8rem 0;
}

.goal p {
  text-align: center;
  padding: .2rem .8rem;
  line-height: 32px;
}

.call {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background-image: url("/public/reviews/contact.jpg");
  background-repeat: none;
  background-color: rgba(0, 0, 0, .5);
  background-size: cover;
  width: 100%;
  height: 70vh;
  margin: 1rem 0;
  background-position: center;
}

.call p {
  background-color: rgba(0, 0, 0, .5);
  padding: .4rem .8rem;
  font-family: "Source Serif Pro", serif;
  color: #fff;
  margin: .8rem 0;
}

.call button {
  background-color: transparent;
  padding: .6rem .8rem;
  border: 2px solid green;
  border-radius: .6rem;
  margin: .3rem 0;
  transition: .7s cubic-bezier(0.19, 1, 0.22, 1);
}

.call button:hover {
  background-color: green;
}

.call button a {
  text-decoration: none;
  text-transform: capitalize;
  letter-spacing: 1.2px;
  color: #fff;
}

.p {
  text-align: center;
  font-size: medium;
}

/* about responsive */
@media (max-width: 600px) {
  .about-wrapper {
    grid-template-columns: 1fr;
  }

  .about-cont h2 {
    font-size: large;
  }

  .about-wrapper p {
    font-size: medium;
  }

  .team {
    grid-template-columns: 1fr;
  }

  .goal {
    margin: .2rem 1rem;
  }

  .team p,
  .goal p {
    font-size: medium;
    text-align: justify;
  }

  .call {
    height: 25vh auto;
    padding: .4rem .6rem;
    background-position: 22%;
    background-size: cover;
  }

  .call p {
    font-size: medium;
    text-align: center;
    border-radius: .4rem;
  }
}

@media (max-width: 480px) {
  .call  {
    background-size: auto;
    background-repeat: no-repeat;
  }
  .team img {
    padding: .5rem;
  }
}

/* End of about page styles */

/* styles for Service Page */

.service-main {
  margin: 0 10rem;
  overflow: hidden;
}

/* .brand is the img inside service-main */

.service-main h2 {
  font-family: "Source Serif Pro", serif;
  text-align: center;
  margin: .8rem 0;
  text-decoration: underline;
  text-underline-offset: 5px;
  font-size: x-large;
  color: #4169e1;
}

.service-main p {
  text-align: justify;
}

.template {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
  font-family: "Source Serif Pro", serif;
  margin: 2rem .6rem;
}

.template img {
  width: 100%;
  padding: .2rem .4rem;
  object-fit: contain;
  height: 40vh;
  border-radius: .8rem;
}

.template .baby {
  shape-outside: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  -webkit-clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  object-position: center;
  object-fit: contain;
}

.template h2 {
  font-family: "Source Serif Pro", serif;
  text-align: center;
  font-size: large;
  font-style: italic;
  color: #4169e1;
}

.template p {
  color: #000;
  font-family: "Source Serif Pro", serif;
  text-align: justify;
  padding: .2rem .6rem;
  font-size: calc(.8rem + .5vw);
}

.web img {
  width: 100%;
  height: 80vh;
  object-fit: contain;
  margin: 1rem 0;
}

.web h2 {
  text-align: center;
  font-size: large;
}

.web p {
  line-height: 32px;
}

.web ol {
  list-style-position: inside;
}

.web ol li {
  font-size: calc(.8rem + .7vw);
  margin: .8rem 0;
  font-family: "Source Serif Pro", serif;
  line-height: 32px;
  text-align: justify;
}

/* Responsive Service Page */

@media (max-width: 950px) {
  .service-main {
    margin: 0 4rem;
  }
}

@media (max-width: 850px) {

  .service-main,
  .web h2,
  .web p {
    overflow: hidden;
    margin: .5rem 0;
  }

  .web img {
    max-width: 100%;
    margin: 0 2rem;
  }

  .service-main h2 {
    font-size: large;
  }

  .service-main p {
    font-size: medium;
  }

  .template {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 290px) {
    .web h2 {
      margin: 0 .5rem;
      padding: .8rem;
    }
    .web img {
      margin: 0 1rem;
    }
    .web p {
      margin: .2rem 1rem;
    }
    .web ol li {
      margin: .2rem 1rem;
    }
}



/* End of styles for Service page */

