/* style for Product Page */

.product-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: .4rem 2rem;
  font-family: "Source Serif Pro", serif;
}

.product-wrapper .product {
  width: 80vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  border-radius: 1rem;
  margin: .4rem 0;
  padding: .6rem .8rem;
}

.product img {
  width: 80%;
}

.product .content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  width: 100%;
  height: 100%;
  background-color: #f1f9ec;
  border-radius: .8rem;
  color: #000;
  padding: 1rem;
}

.product .content h2 {
  font-size: larger;
  color: #4169e1;
  padding: .2rem .6rem;
  text-transform: capitalize;
  text-align: left;
}

.product .content .label {
  background-color: #add8e6;
  padding: .2rem .4rem;
  border-radius: .5rem;
  margin: .2rem .4rem;
  color: #fff;
}

.product .content .price {
  display: inline-flex;
  padding: .4rem .8rem;
}

.product .content .price p {
  padding: 0 .8rem;
  font-size: xx-large;
  letter-spacing: .5px;
  color: #4169e1;
}

.product .content .price .fake {
  opacity: .5;
  font-size: x-large;
  color: #000;
  text-decoration: line-through;
}

.product .content ul {
  list-style-position: inside;
  font-size: medium;
  margin: .4rem 0;
  line-height: 30px;
}

.product .content ol li {
  font-family: "Source Serif Pro", serif;
  letter-spacing: .5px;
  margin: .4rem 0;
  text-transform: uppercase;
}

.product .content .callme {
  display: flex;
  align-items: center;
  justify-content: center;
}

.callme .order {
  background-color: #0f4d92;
  position: relative;
  left: 10%;
  width: 80%;
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
}

.callme .order h2 {
  color: #fff;
  font-size: larger;
  letter-spacing: 1.5px;
  padding: .4rem;
  text-align: center;
  text-transform: uppercase;
  font-family: "Source Serif Pro", serif;
}

.callme .order button {
  background-color: transparent;
  padding: .4rem .6rem;
  margin: .4rem 0;
  color: #fff;
  font-size: large;
  border: 2px solid green;
  border-radius: .5rem;
  cursor: pointer;
  transition: .75s cubic-bezier(0.19, 1, 0.22, 1);
}

.callme .order button:hover {
  background-color: green;
  color: #fff;
}

/* Product Responsive */

@media (max-width: 750px) {
    .product-wrapper .product {
      grid-template-columns: 1fr;
    }
    .product .content .price p {
      font-size: 1.5rem;
    }
    .product .content .price .fake {
      font-size: 1.2rem;
      align-items: center;
    }
    .product .content .callme {
      margin-left: 25%;
      margin-top: .8rem;
    }
}

/* End of product Page */

/* Start of privacy Policy page */

