/* Styles for MyForm Page */

.main-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.main-form form {
  background-color: #003366;
  display: flex;
  flex-direction: column;
  width: 60%;
  min-width: 250px;
  height: 60vh auto;
  min-height: 250px;
  text-align: center;
  padding: .2rem .6rem;
  margin: 1rem .8rem;
  border-radius: 1rem;
}

.main-form form h2 {
  color: #fff;
  font-size: larger;
  margin: .2rem 0;
  text-transform: uppercase;
  font-family: "Source Serif Pro", serif;
}

.main-form form input[type="text"] {
  align-items: center;
  justify-content: center;
  width: 60%;
  background-color: transparent;
  padding: .6rem .4rem;
  margin: .6rem auto;
  color: #fff;
  outline: none;
  border: 2px solid #0892d0;
  border-radius: .5rem;
  font-size: medium;
  font-family: "Source Serif Pro", serif;
}

.main-form form input[type="text"]:hover {
  border-color: green;
}

.main-form form input[type="text"]::placeholder {
  color: #f1f9ec;
  text-transform: capitalize;
}

.main-form form button {
  background-color: transparent;
  width: 40%;
  padding: .5rem .4rem;
  margin: .4rem auto;
  outline: none;
  border: 2px solid green;
  border-radius: .5rem;
  color: #fff;
  font-size: medium;
  cursor: pointer;
  transition: .75s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.main-form form button:hover {
  background-color: green;
}

/* End of Styles for MyForm Page */