* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}

.model-container {
  position: absolute;
  top: -30%;
  left: -13%;
  width: 99vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}

.model-container .model-wrapper {
  width: 40vw;
  height: 50vh;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  z-index: 1;
  border-radius: 5%;
}

.model-wrapper .close {
  color: #000;
}

.model-wrapper .close:hover {
  color: #4169e1;
}

.model-wrapper .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.model-wrapper .content > div {
  margin: .4rem 0;
  display: inline-flex;
}

.model-wrapper .content .icons {
  font-size: x-large;
}

.model-wrapper .content p {
  display: inline-block;
  font-size: x-large;
  padding: 0 .2rem;
}

/* responsive Model */
@media (max-width: 800px) {
  .model-container .model-wrapper {
    width: 70%;
    height: 50%;
    top: 25%;
  }
  .model-wrapper .content p {
    font-size: large;
  }
}

@media (max-width: 280px) {
   .model-container .model-wrapper {
    width: 90%;
    height: 30%;
   }
}